import React, { Component } from "react";
import ReactTable from "react-table";
import "react-table/react-table.css";
import { Layout } from "../../Components/Layout";

const data = [
  {
    name: "Roy Agasthyan",
    age: 26
  },
  {
    name: "Sam Thomason",
    age: 22
  },
  {
    name: "Michael Jackson",
    age: 36
  },
  {
    name: "Samuel Roy",
    age: 56
  },
  {
    name: "Rima Soy",
    age: 28
  },
  {
    name: "Suzi Eliamma",
    age: 28
  }
];

class UsersTable extends Component {
  constructor(props) {
    super(props);
    this.columns = [
      {
        Header: "Name",
        accessor: "name"
      },
      {
        Header: "Age",
        accessor: "age"
      }
    ];
  }
  render() {
    return (
      <Layout>
        <ReactTable
          data={data}
          columns={this.columns}
          defaultPageSize={3}
          pageSizeOptions={[3, 6]}
          className="custom_users_table"
        />
      </Layout>
    );
  }
}

export default UsersTable;
